:root {
  --color-light-xl: #ffffff;
  --color-light-l: #F6F6F6;
  --color-light-s: #FAFAFA;
  --color-dark-s: #CDCDCD;
  --color-dark-m: #B2B2B2;
  --color-dark-l: #545454;
  --color-dark-xl: #4C4C4C;
  --color-dark-2xl: #444444;
  --color-dark-3xl: #292929;
  --color-primary: #F1CDB3;
  --color-primary-light: #FDDCC4;
  --start-screen-gradient-background: url(/img/noise_lines_transparent@2X.png),
      radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
      #211F20;
  --footer-gradient-background: url(/img/noise_lines_transparent@2X.png),
      radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
      #211F20;
}

.h2 {
  font-family: Georgia;
  font-style: normal;
  font-size: 44px;
  line-height: 130%;
}

.h3 {
  font-family: Georgia;
  font-style: normal;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h3-mobile {
  font-family: Georgia;
  font-style: normal;
  font-size: 25px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h4 {
  font-family: Georgia;
  font-style: normal;
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h5 {
  font-family: Georgia;
  font-style: normal;
  font-size: 15px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h5-modal-window {
  font-family: Georgia;
  font-weight: bold;
  font-size: 15px;
  line-height: 130%;
  letter-spacing: 6%;
}

.paragraph-l {
  font-family: Arial;
  font-size: 15px;
  line-height: 160%;
}

.paragraph-s {
  font-family: Arial;
  font-size: 12px;
  line-height: 150%;
}

.paragraph-mobile-menu {
  font-family: Arial;
  font-size: 32px;
  line-height: 160%;
}

.logo-title {
  font-family: Georgia;
  font-weight: bold;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: 6%;
}

.logo-subtitle {
  font-family: Arial;
  font-weight: bold;
  font-size: 13px;
  line-height: 100%;
  letter-spacing: 10%;
}

html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("ArialMT"), url("../fonts/ArialMT.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("Arial-BoldMT"), url("../fonts/Arial-BoldMT.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia"), url("../fonts/Georgia.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia"), url("../fonts/Georgia.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 26px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  color: var(--textColor);
  font-style: normal;
  background: var(--white);
}

main {
  flex-grow: 1;
  background-color: var(--white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body {
  position: relative;
}

.table-responsive {
  overflow: hidden;
  overflow-x: auto;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1364px;
  margin-left: auto;
  margin-right: auto;
}